/* ----------------------------------------------------------------
	Add here styles for variables. Thanks!
-----------------------------------------------------------------*/


//-------------------- Base Colors --------------------//

$theme-color:                   #d19ca3;
$body-color:                    #FFFFFF;
$heading-color:                 #FFFFFF;

$body-bg:                       #201b30;


//-------------------- Font Families --------------------//

$body-font:                     'Montserrat', sans-serif;
$heading-font:                  'Syncopate', sans-serif;
$third-font:                    'Crafty Girls', cursive;


//-------------------- Redefine Bootstrap Variables ------------//

// Theme Colors
$primary:                       $theme-color;
$secondary:                     #5b5571;

// Link
$link-color:                    $theme-color;
$link-decoration:				none;
$link-hover-color:				$theme-color;

// Container Width
$container-max-widths: (
  xxl: 1140px
);

// Border
$border-radius:					0;

// Font sizes
$font-size-base:                1.0625rem;

$h1-font-size:                  5.125rem;
$h2-font-size:                  3rem;
$h3-font-size:                  1.875rem;
$h4-font-size:                  1.5rem;
$h5-font-size:                  $font-size-base;
$h6-font-size:                  0.75rem;

// Font Weight
$font-weight-base:              300;

// Line Height
$line-height-base:            	30 / 17;
$line-height-sm:              	42 / 30;
$line-height-lg:              	24 / 14;

// Forms
// $input-color:                   #d0d0d0;
$input-color:                   #5b5571;
$input-border-color:            $theme-color;
$input-border-width:			      0;
$input-btn-focus-color-opacity: 1;

$input-btn-padding-y:           1.5rem;
$input-btn-padding-x:           2.125rem;
$input-btn-font-size:           0.875rem;

$input-btn-padding-y-sm:        1rem;
$input-btn-padding-x-sm:        1.416rem;
$input-btn-font-size-sm:        0.583rem;

$input-btn-padding-y-lg:        2rem;
$input-btn-padding-x-lg:        2.83rem;
$input-btn-font-size-lg:        1.16rem;

$input-btn-line-height:         1;

$input-btn-focus-width:         1px;
$input-focus-border-color:      $theme-color;

// Buttons
$btn-font-family:               $heading-font;
$btn-padding-y:              	$input-btn-padding-y;
$btn-padding-x:              	$input-btn-padding-x;
$btn-font-size:              	$input-btn-font-size;

$btn-padding-y-sm:              $input-btn-padding-y-sm;
$btn-padding-x-sm:              $input-btn-padding-x-sm;
$btn-font-size-sm:              $input-btn-font-size-sm;

$btn-padding-y-lg:              $input-btn-padding-y-lg;
$btn-padding-x-lg:              $input-btn-padding-x-lg;
$btn-font-size-lg:              $input-btn-font-size-lg;

$btn-font-weight:               700;
$btn-line-height:               1;

$btn-border-width:              2px;
$btn-border-radius:             0;


// Tabs
$nav-link-color:							#ffffff;
$nav-tabs-border-color:                   	$theme-color;
$nav-tabs-border-width:                  	2px;
$nav-tabs-border-radius:                  	0;
$nav-tabs-link-hover-border-color:        	$theme-color;
$nav-tabs-link-active-color:              	$theme-color;
$nav-tabs-link-active-border-color:       	$theme-color;


// Accordion
$accordion-padding-y:                     	1.875rem;
$accordion-padding-x:                     	2.25rem;
$accordion-color:                         	$theme-color;
$accordion-bg:                            	rgba($theme-color, 0.3);
$accordion-border-width:                  	0;
$accordion-border-radius:                 	0;

$accordion-button-color:                  	$body-color;
$accordion-button-bg:                     	transparent;
$accordion-button-active-bg:              	transparent;
$accordion-button-active-color:           	$theme-color;

$accordion-button-focus-border-color:     	none;
$accordion-button-focus-box-shadow:       	none;

$accordion-icon-width:                    	1.25rem;
$accordion-icon-color:                    	$body-color;


// Pagination
$pagination-bg:								transparent;
$pagination-padding-y:                    	1rem;
$pagination-padding-x:                    	1rem;
$pagination-border-radius:                	0.5rem;
$pagination-border-width:                 	0;

$pagination-hover-color:                  	#FFFFFF;
$pagination-hover-bg:                     	$theme-color;

$pagination-active-color:                 	#FFFFFF;
$pagination-active-bg:                    	$theme-color;


//-------------------- Top Bar --------------------//

$topbar-height:                 2.8125rem;
$topbar-line-height:            2.8125rem;
$topbar-color:                  #5b5571;
$topbar-hover-color:            $theme-color;


//-------------------- Header --------------------//

$header-height:                 5.25rem;

// Sticky Header
$sticky-header-height:          2.8125rem;


//-------------------- Primary Menu --------------------//

$primary-menu-padding:			2.8125rem;
$sticky-menu-padding:			1.875rem;

$primary-menu-font:             $body-font;
$primary-menu-font-weight:      400;
$primary-menu-font-size:        0.875rem;
$primary-menu-font-tt:          initial;
$primary-menu-font-color:       #FFFFFF;
$primary-menu-font-spacing:     0;

// top Button
$top-btn-font-size:        		1.125rem;
$top-btn-font-color:       		$heading-color;

// Dropdown BG
$dropdown-bg:					#252525;


//-------------------- Section --------------------//

$section-spacer:				6.25rem;


//-------------------- SVG Icons --------------------//

